import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';

const VisualLink = ({points, location}) => { 
    if (!location) {
        return '';
    }
    return (
        <svg className="annotation-links">
          <linearGradient id="AnnotationGradient">
            <stop offset="5%" stopColor="#a4c3e55c" />
            <stop offset="95%" stopColor="#80aaff36" />
          </linearGradient>
          <polygon className="visual-link" points={points}/>
          <rect className="visual-overlay" 
                x={location.x} y={location.y}
                width={location.width} height={location.height}/>
        </svg>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(VisualLink);
