import {
    setShowLFOs,
    setShowEnvelopes,
    setLeftSidebarWidth,
    setShowFormantEffect,
    setSelectedFMEnvelopeType,
    setSelectedFMFormant,
    setSelectedFMOperatorDetails,
    setSelectedZObject,
    setShowTrackInstrument,
    setRightSidebarOption,
    setShowEditorBottom} from 'actions/index.js';
import {MULTI_SYNTH} from 'constants/instrument-types.js';
import {link} from './link.js';

const getSynth = (engine) => {
   let track = engine
        .tracks
        .find(
            x =>
            x.instrument &&
                x.instrument.getInstrumentType() === MULTI_SYNTH);
    return track;
};

const dependency = (state, engine) => {
    let track = getSynth(engine);
    if (!track) {
        return false;
    }
    return state.zobject.selectedZObject === track;
};

const action = (dispatch, engine) => {
    let track = getSynth(engine);
    track = track || engine.addMultiSynthTrack();
    engine.selectTrack(track.trackNumber);
    dispatch(setShowLFOs(false));
    dispatch(setShowEnvelopes(false));
    dispatch(setShowFormantEffect(undefined));
    dispatch(setSelectedZObject(track));
    dispatch(setLeftSidebarWidth(50));
    dispatch(setShowTrackInstrument(true));
    dispatch(setShowEditorBottom(undefined));
    dispatch(setRightSidebarOption("Audition"));
    dispatch(setSelectedFMOperatorDetails(undefined));
    track.setIsEnabled(true);
};
         
