import React, { useEffect, useCallback, useState} from 'react';
import DocLink from "components/documentation/DocLink.js";
import {connect} from 'react-redux';

const AnnotatedDescription = ({links, goToLink, description}) => { 
    if (!Array.isArray(description)) {
        description = [description];
    }
    return (
        <div className="part-description">
          {description.map(x => <Line line={x} goToLink={goToLink} links={links}/>)}
        </div>);
};

const Line = ({goToLink, line, links}) => {
    let spans = [];
    let current = "";
    for (let i=0; i < line.length; i++) {
        let char = line[i];
        if (char === "[") {
            spans.push(<span>{current}</span>);
            current = "";
        } else if (char === "]") {
            let link = current;
            let name = links.find(x => x[0] === link)[1];
            let subSection = links.find(x => x[0] === link)[2];
            spans.push(<DocLink text={link} name={name} goToLink={goToLink} subSection={subSection}/>);
            current = "";
        } else {
            current += char;
        }
    }

    spans.push(current);

    return (
        <div>
          {spans}
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(AnnotatedDescription);
