import {
    setShowLFOs,
    setShowEnvelopes,
    setShowTrackInstrument,
    setRightSidebarOption,
    setShowPadSequencer,
    setShowEditorBottom} from 'actions/index.js';
import {link} from './link.js';

const action = (dispatch, engine) => {
    if (engine.tracks[0] && !engine.tracks[0].isWarped()) {
        engine.tracks[0].warp();
    }
    dispatch(setShowLFOs(false));
    dispatch(setShowEnvelopes(false));
    dispatch(setShowTrackInstrument(false));
    dispatch(setRightSidebarOption("Tools"));
    dispatch(setShowEditorBottom(undefined));
    dispatch(setShowPadSequencer(undefined));
};
         
export const WARP = {
    "Warp Button": {
        className: "warp-button",
        tags: "Sidebar",
        interactive: true,
        selectTrack: true,
        location: {x: 240, y: 55, width: 300, height: 300},
        action,
        description: [
            "Click to toggle warping on & off.",
            "When warped, the sample will be timestretched (w/o changing its pitch) to match the project tempo."
        ]
    },
    "Warp Tempo": {
        className: "warp-knob",
        tags: "Sidebar",
        interactive: true,
        selectTrack: true,
        location: {x: 240, y: 55, width: 300, height: 300},
        action,
        description: [
            "Shows the warped tempo of the sample.",
            "To adjust this tempo, click and drag up and down.",
            "Changing with the  or ctrl key down, and [steps selected] will [parameter-lock] that step to that tempo."
        ],
        links: [link("steps selected", "Step Sequencer", "Selecting Steps"),
                link("parameter-lock", "Audio Effects", "Parameter-Locking")]

    },
    "Warp Factor": {
        className: "warp-factor",
        tags: "Sidebar",
        interactive: true,
        selectTrack: true,
        location: {x: 240, y: 55, width: 300, height: 300},
        action,
        description: [
            "The up arrow doubles the tempo of the sample.",
            "The down arrow halves the tempo of the sample.",
            "Clicking with the  or ctrl key down, and [steps selected] will [parameter-lock] that step to that tempo."
        ],
        links: [link("steps selected", "Step Sequencer", "Selecting Steps"),
                link("parameter-lock", "Audio Effects", "Parameter-Locking")]
    },
};
 
