import React, { useRef, useEffect, useCallback, useState} from 'react';
import AnnotatedDescription from "./AnnotatedDescription.js";
import VisualLink  from './VisualLink.js';
import {useBorderRadius} from './useBorderRadius.js';
import {connect} from 'react-redux';
import './Annotation.scss';
import {
    setDocumentationSubSection,
    setSelectedZObject} from 'actions/index.js';
import {LAYOUT_MODE, INTERACT_MODE} from './modes.js';

const NORTH = 'NORTH';
const EAST = 'EAST';
const WEST = 'WEST';
const SOUTH = 'SOUTH';


const DEFAULT_LOCATION = {x: 300, y: 100, width: 300, height: 300};

const AnnotatedEditor = ({
    setDocumentationSubSection,
    parts,
    setSelectedZObject,
    dependency,
    showPadSequencer,
    mode,
    setMode,
    //selected,
    setSelected,
    numTracks,
    sequencerEngine,
    leftSidebarWidth,
    boundingRef,
    goToLink,
    numEffects,
    position,
    subSection
}) => { 

    let ref = useRef(undefined);
    let [location, setLocation] = useState(undefined);
    let [annotationLocation, setAnnotationLocation] = useState({x: 300, y: 100, width: 300, height: 300});

    let name = subSection; //Object.keys(parts)[selected];
    let selected = Object.keys(parts).indexOf(name);
    if (selected === -1) {
        selected = 0;
    }
    if (parts[name] === undefined) {
        name = Object.keys(parts)[0];
    }
    let {selectTrack, isSynth, interactive, className, description, links} = parts[name || Object.keys(parts)[0]];

    useEffect(() => {
        if (selectTrack && numTracks > 0) {
            setSelectedZObject(sequencerEngine.tracks.filter(x => isSynth ? x.instrument : !x.instrument)[0]);
        }
    }, [selectTrack, selected, numTracks, isSynth]);

    useEffect(() => {
        if (interactive && !isSynth) {
            setAnnotationLocation({x: 600, y: 40, width: 300, height: 300});
            if (sequencerEngine.tracks.filter(x => !x.instrument).length === 0) {
                sequencerEngine.searchForSounds(["kick"]);
            }
        } else {
            setAnnotationLocation({x: 300, y: 100, width: 300, height: 300});
        }
    }, [isSynth, sequencerEngine, mode, selected, setAnnotationLocation, interactive]);

    useEffect(() => {
        let part = parts[Object.keys(parts)[selected]];
        if (!part) {
            return;
        }
        let {action, selectTrack, dependency, location} = part;
        if (action) {
            if (selectTrack && (!sequencerEngine.subSequences[0].pads[0] || !sequencerEngine.tracks[0])) {
                // dont do anything here
            } else {
                action((x) => sequencerEngine.dispatch(x), sequencerEngine);
            }
        }
    }, [selected, numTracks]);

    useEffect(() => {
        let part = parts[Object.keys(parts)[selected]];
        if (!part) {
            return;
        }
        if (part.location) {
            setAnnotationLocation(part.location);
        } 
    }, [location, selected, parts, setAnnotationLocation ]);

    useEffect(() => {
        if (!interactive) {
            setSelectedZObject(undefined);
        } else if (interactive) {
            setSelectedZObject(sequencerEngine.tracks[0]);
        }
    }, [interactive]);

    useEffect(() => {
        let element = Array.isArray(className) ?
            className.map(x => document.getElementsByClassName(x)[0]).filter(x => x)[0] :
            document.getElementsByClassName(className)[0];
       if (element) {
            setLocation(getLocation(element, boundingRef.current));
        } else {
            setTimeout(() => {
                let element = Array.isArray(className) ?
                    className.map(x => document.getElementsByClassName(x)[0]).filter(x => x)[0] :
                    document.getElementsByClassName(className)[0];
                if (element) {
                    setLocation(getLocation(element, boundingRef.current));
                } else {
                    setLocation(undefined);
                }
            }, 500);
        }
    }, [className, setLocation, leftSidebarWidth, dependency, numEffects, position]);

    let {direction, points, style} = useBorderRadius({annotationLocation, location});

    return [
        <VisualLink location={location} points={points}/>,
        <div style={{
            ... style,
            top: annotationLocation.y,
            left: annotationLocation.x
        }}
             ref={ref} className="annotated-part">
          <div className="part-name">{name}</div>
          <AnnotatedDescription description={description} goToLink={goToLink} links={links}/>
          {<div className="nav-buttons">
            <div className="arrow-container">
              <div
                onClick={() => setDocumentationSubSection(Object.keys(parts)[selected-1])}
                className={selected === 0 ? "pushable disabled arrow-left" : "pushable arrow-left"}/>
            </div>

           <div className="arrow-container">
              <div
                onClick={() => setDocumentationSubSection(Object.keys(parts)[selected+1])}
                className={selected === Object.keys(parts).length - 1 ? "pushable disabled arrow-right" : "pushable arrow-right"}/>
            </div>
            <div className="current-number">
              {selected + 1} <span>of</span> {Object.keys(parts).length}
            </div>
           </div>}
        </div>
   ];
};

const getLocation = (element, bounding) => {
    var rect = element.getBoundingClientRect();
    let bound = bounding.getBoundingClientRect();
    let y = rect.top - bound.top;
    let x = rect.left - bound.left;
    let height = element.offsetHeight;
    let width = element.offsetWidth;
    let pos = {x, y, width, height};
    return pos;
};

const mapStateToProps = (state, props) => {
    let dependency = false;
    let parts = props.parts;
    let part = parts[Object.keys(parts)[props.selected]];
    if (part.dependency) {
        dependency = part.dependency(state, props.sequencerEngine);
    }
    let selectedZObject = state.zobject.selectedZObject;
    return {
        position: selectedZObject && state.windows.windowPositions[selectedZObject.id],
        dependency,
        numTracks: props.sequencerEngine.tracks.length,
        showPadSequencer: state.spatial.showPadSequencer,
        leftSidebarWidth: state.spatial.leftSidebarWidth,
        numEffects: props.sequencerEngine.tracks[0] &&
            (props.sequencerEngine.tracks[0].effects.length +
             props.sequencerEngine.tracks[0].midiEffects.effects.length),
      subSection: state.documentation.subSection
    };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setSelectedZObject: x => dispatch(setSelectedZObject(x)),
      setDocumentationSubSection: x => dispatch(setDocumentationSubSection(x))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(AnnotatedEditor);
