import React, { useEffect, useState, Component } from 'react';
import ethereum from 'decentralized/ethereum.js';
import DocumentationSidebar from './DocumentationSidebar.js';
import DocumentationViewer from './DocumentationViewer.js';
import '../../Documentation.scss';
import './mobile.scss';

const Documentation = (props) => {
    let [documentationToShow, setDocumentationToShow] = useState('Getting Started');
    let [innerSidebar, setInnerSidebar] = useState([]);

    useEffect(() => {
        window.documentation = true;
        ethereum.store = props.store;
        ethereum.crypto.store = props.store;
    }, []);

    return (
        <div className="documentation">
          <img onClick={() => props.goBack()} src="/smol-logo4.svg" className="logo-btn"/>
          <DocumentationSidebar
            innerSidebar={innerSidebar}
            onClick={(name) => setDocumentationToShow(name)}
            toShow={documentationToShow} />
          <DocumentationViewer
            setInnerSidebar={setInnerSidebar}
            store={props.store}
            goToLink={(name) => {
                setDocumentationToShow(name);
                window.scrollTo(0,0);
            }}
            toShow={documentationToShow}/>
        </div>
    );
}

export default Documentation;
