import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import {setDocumentationSubSection} from 'actions/index.js';


const AnnotatedSidebar = ({
    subSection,
    parts,
    setSelected,
    selected,
    setDocumentationSubSection,
    notSelected,
    searching,
}) => { 
    useEffect(() => {
        if (notSelected && Object.keys(parts)[selected] !== subSection) {
            let indexOf = Object.keys(parts).indexOf(subSection);
            if (indexOf > -1) {
                setSelected(indexOf);
            }
        }
    }, [subSection, selected, parts, setSelected, searching, notSelected]);
    return (
        <div className="annotated-sidebar">
          {Object.keys(parts).map(
              (name, i) =>
              <div
                onClick={() => {
                    setDocumentationSubSection(name);
                }}
                className={!notSelected && (name===subSection || (!searching && selected === i)) ? "selected option" : "option"}>
                {name} 
              </div>)}
        </div>
    );
};

const tags = (_tags) => {
    if (!_tags) {
        return '';
    }
    if (!Array.isArray(_tags)) {
        _tags = [_tags];
    }
    return _tags.map(x => <div className="tag">{x}</div>);
}

const mapStateToProps = (state, props) => {
  return {
      subSection: state.documentation.subSection
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
        setDocumentationSubSection: x => dispatch(setDocumentationSubSection(x))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(AnnotatedSidebar);
