import React, { useRef, useEffect, useCallback, useState} from 'react';
import AnnotatedEditor from "components/documentation/annotated/AnnotatedEditor.js";
import MainView from "components/v3/MainView.js";
import {connect} from 'react-redux';
import './GettingStarted.scss';
import AnnotatedSidebar from '../annotated/AnnotatedSidebar.js';

const DocSection = ({
    setInnerSidebar,
    sequencerEngine,
    children,
    parts,
    subSection,
    goToLink}) => { 
    let ref = useRef();
    let [selected, setSelected]  = useState(0);
    window.killScrolling = true;

    useEffect(() => {
        if (Object.keys(parts)[selected] !== subSection) {
            let indexOf = Object.keys(parts).indexOf(subSection);
            if (indexOf > -1) {
                setSelected(indexOf);
            }
        }
    }, [subSection, selected, parts, setSelected]);
 
    useEffect(() => {
        setInnerSidebar(
            <AnnotatedSidebar
              parts={parts}
              selected={selected}
              setSelected={setSelected}/>
        );

        return () => setInnerSidebar(undefined);
    }, [selected, setSelected, setInnerSidebar]);

    return (
        <div className="getting-started">
          <div className="section-text">
            {children}
          </div>
          <div className="learn-the-editor" ref={ref}>
            {sequencerEngine &&
             <MainView
               isDocumentation={true}
               store={sequencerEngine.store}
               sequencerEngine={sequencerEngine}/>}
            {sequencerEngine &&
             <AnnotatedEditor
               selected={selected}
               parts={parts}
               setSelected={setSelected}
               goToLink={goToLink}
               boundingRef={ref}
               sequencerEngine={sequencerEngine}
             />}
          </div>
        </div>);
}

const mapStateToProps = (state, props) => {
  return {
      subSection: state.documentation.subSection
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(DocSection);
