import React from 'react';
import {connect} from 'react-redux';
import {setDocumentationSubSection} from 'actions/index.js';

const DocLink = ({goToLink, name, text, subSection, setDocumentationSubSection}) => {
    return (
        <span
          onClick={() => {
              goToLink(name);
              setDocumentationSubSection(subSection);
          }}
          className="doc-link">{text || name}</span>
    );
};

const mapDispatchToProps = (dispatch) => {
    return {
        setDocumentationSubSection: x => dispatch(setDocumentationSubSection(x))
    };
};

export default connect(null, mapDispatchToProps)(DocLink);
