import React, { useState, useEffect, useCallback} from 'react';
import AnnotatedSidebar from "components/documentation/annotated/AnnotatedSidebar.js";
import {DOCUMENTATION_MAP} from './constants.js';
import {index} from './annotated/definitions/index.js';
import './search.scss';

const DocumentationSidebar = ({onClick, toShow, innerSidebar}) => {
    let [searchTerm, setSearchTerm] = useState('');
    let [selected, setSelected] = useState(0);

    let results = {};
    if (searchTerm !== "") {
        let _searchTerm = searchTerm.toLowerCase();
        for (let name in index) {
            let section = index[name];
            let matchingSubSections = {};
            for (let subSectionName in section) {
                let subSection = section[subSectionName];
                let desc = subSection.description;
                if (!Array.isArray(desc)) {
                    desc = [desc];
                }
                if (name.toLowerCase().includes(_searchTerm) ||
                    subSectionName.toLowerCase().includes(_searchTerm) ||
                    desc.some(x => x.toLowerCase().includes(_searchTerm))) {
                    matchingSubSections[subSectionName] = subSection;
                }
            }
            if (Object.keys(matchingSubSections).length > 0) {
                results[name] = matchingSubSections;
            }
        }
    }
    
    return (
        <div className="doc-sidebar">
          <div className="search-container">
            <img src="/search.svg"/>
            <input
              className="search-input"
              value={searchTerm}
              onChange={e => setSearchTerm(e.target.value)}
            />
            <div onClick={() => setSearchTerm("")} className="pushable clear-btn">x</div>
          </div>
          {Object.keys(DOCUMENTATION_MAP)
           .filter(x => searchTerm === "" ||
                   results[x])
           .map(
               name =>
               <div
                 onClick={() => onClick(name)}
                 className={name === toShow ? "topic selected" : "topic"}>
                 {name}
                 {searchTerm !== "" ?
                  <AnnotatedSidebar
                    searching={true}
                    notSelected={name !== toShow}
                    setSelected={setSelected}
                    selected={selected}
                    parts={results[name]}
                  /> :
                  name === toShow && innerSidebar}
               </div>)}
        </div>
    );
}

export default DocumentationSidebar;
