import {
    setLeftSidebarWidth,
    setShowTrackInstrument,
    setSelectedZObject,
    setShowEditorBottom
} from 'actions/index.js';
import * as modes from 'constants/editor-bottom-modes.js';
import {link} from './link.js';

const action = (dispatch) => {
    dispatch(setShowTrackInstrument(false));
    dispatch(setLeftSidebarWidth(50));
    dispatch(setShowEditorBottom(modes.PIANO_ROLL));
};

export const PIANO_ROLL = {
    "Piano Roll": {
        className: "editor-bottom show piano-roll-mode",
        dependency: (state, engine) => state.spatial.showEditorBottom === modes.PIANO_ROLL &&
            engine.tracks[0].length > 0,
        tags: "Effect",
        location: {x: 640, y: -120, width: 300, height: 300},
        selectTrack: true,
        interactive: true,
        action,
        description: [
            "The piano roll is shown here, at the bottom.",
            "You can scroll up and down and pinch to zoom in and out."
        ],
        links: [link("track\'s", "Track", "Pad Element")]
    },
    "Adding a note": {
        className: "grid-steps-container",
        dependency: (state, engine) => state.spatial.showEditorBottom === modes.PIANO_ROLL &&
            engine.tracks[0].length > 0,
        tags: "Effect",
        location: {x: 640, y: -120, width: 300, height: 300},
        selectTrack: true,
        interactive: true,
        action,
        description: [
            "Click anywhere in a row to add a note.",
            "Click on the added note to delete it."
        ],
        links: [link("track\'s", "Track", "Pad Element")]
    },
    "Cursor": {
        className: "time-grid-top",
        dependency: (state, engine) => state.spatial.showEditorBottom === modes.PIANO_ROLL &&
            engine.tracks[0].length > 0,
        tags: "Effect",
        location: {x: 640, y: -120, width: 300, height: 300},
        selectTrack: true,
        interactive: true,
        action,
        description: [
            "Click anywhere on the ruler above the piano roll sets the cursor.",
            "When the cursor is set, and restart the sequencer, it will start from this point.",
            "When copy and pasting steps, the pasted steps will go at the cursor."
        ],
        links: [link("track\'s", "Track", "Pad Element")]
    },
    "Velocity": {
        className: "piano-roll-control",
        dependency: (state, engine) => state.spatial.showEditorBottom === modes.PIANO_ROLL &&
            engine.tracks[0].length > 0,
        tags: "Effect",
        location: {x: 640, y: -120, width: 300, height: 300},
        selectTrack: true,
        interactive: true,
        action: (dispatch, engine) => {
            action(dispatch, engine);

            engine.scripting.viScripting.t();
            engine.scripting.viScripting.l();
            engine.scripting.viScripting.t();
            engine.scripting.viScripting.l();
            engine.scripting.viScripting.t();
            engine.scripting.viScripting.l();
        },
        description: [
            "The vertical bars at the bottom of the piano roll let you control the velocity of each note."
        ],
        links: [link("track\'s", "Track", "Pad Element")]
    },
};

