import {
    setLeftSidebarWidth,
    setShowLFOs,
    setShowEnvelopes,
    setShowTrackInstrument,
    setSelectedZObject,
    setShowEditorBottom
} from 'actions/index.js';
import * as modes from 'constants/editor-bottom-modes.js';
import {link} from './link.js';

const action = (dispatch, engine) => {
    dispatch(setShowLFOs(false));
    dispatch(setLeftSidebarWidth(50));
    dispatch(setShowEnvelopes(false));
    dispatch(setShowEditorBottom(modes.EFFECTS));
    dispatch(setShowTrackInstrument(false));
};

