import {
    setLeftSidebarWidth,
    setShowLFOs,
    setShowEnvelopes,
    setShowTrackInstrument,
    setShowEditorBottom,
    setRightSidebarOption,
    setShowPadSequencer} from 'actions/index.js';
import {link} from './link.js';

const action = (dispatch) => {
    dispatch(setShowLFOs(false));
    dispatch(setShowEnvelopes(false));
    dispatch(setShowTrackInstrument(false));
    dispatch(setLeftSidebarWidth(240));
    dispatch(setShowEditorBottom(undefined));
    dispatch(setRightSidebarOption("Tools"));
    dispatch(setShowPadSequencer(true));
};


export const PAD_SEQUENCER_BASICS = {
   "Pad Sequencer": {
        className: "pad-sequencer",
        tags: "Pad Sequencer",
       selectTrack: true,
        interactive: true,
        location: {x: -80, y: 55, width: 300, height: 300},
        dependency: (state) => state.spatial.showPadSequencer,
       action,
        description: [
            "When you start [adding sounds], you\'ll be brought to the [Pad Sequencer View].",
            "You can edit the patterns for each track inside a [pad] here."
        ],
       links: [
           link("adding sounds", "Getting Started", "Add a Sound"),
           link("pad", "Pad"),
               link("Pad Sequencer View", "Pad Sequencer")
              ]
    },
};

