import React, { useState, useEffect, useCallback, Component } from 'react';
import {useSequencer} from "components/v2/useSequencer.js"
import Modals from 'components/v2/Modals.js';
import KeyListener from 'components/KeyListener.js';
import Scripting from 'scripting/Scripting.js';
import {DOCUMENTATION_MAP} from './constants.js';
import ScriptableSequencer from 'engine/ScriptableSequencer.js';
import KeyboardEngine from 'keyboard/KeyboardEngine.js';

const DocumentationViewer = (props) => {
    let [ethereumCache, sequencerEngine, ethereumSearcher] = props.sequencerEngine ? [undefined, sequencerEngine, undefined] :
        useSequencer(props, undefined, false);

    useEffect(() => {
        window.scrollTo(0,0);
    }, [props.toShow]);

    let className = "doc-viewer";
    if (!sequencerEngine) {
        return [];
    }
    if (props.toShow === "Midi Effects") {
        className += " wide midi-effects-viewer";
    }
    return (
        <div className={className}>
          <div color="radial-gradient(75.29% 75.29% at 64.96% 24.36%, #F0DFA7 0.52%, #FDBDAA 18.75%, #FEA186 34.38%, #F09277 49.48%, #D27584 74.48%, #6B7B8A 100%)" className="coke-machine-glow"/>
          <Modals sequencerEngine={sequencerEngine}/>
          <div className="header">
            {props.toShow}
              </div>
          {DOCUMENTATION_MAP[props.toShow](sequencerEngine, props.goToLink, props.setInnerSidebar)}
        </div>
    );
};

const mapStateToProps = (state) => {
    return {
    }
};

export default DocumentationViewer;
