import React, { useRef, Component } from 'react';
import TrackComponent from "components/v2/TrackComponent.js";
import { connect } from "react-redux";
import PlayButton from 'components/user/PlayButton.js';
import StepSequencer from 'components/v2/StepSequencer.js';
import ContextualHelpComponent from 'components/sidebar/ContextualHelpComponent.js';

const MiniTrackComponent = (props) => {
    let ref = useRef();
    const play = ()=> {
        props.sequencerEngine.play();
    };

    return (
        <div ref={ref}className="mini-sequencer">
          <TrackComponent forceShow={true} scrollContainer={ref} track={props.sequencerEngine.tracks[0]}/>
          <div className="play-container">
            <PlayButton isPlaying={props.isPlaying} play={() => play()}/>
            <ContextualHelpComponent/>
          </div>
        </div>
    );
}

const mapStateToProps = (state, props) => {
    let id = props.sequencerEngine.getSequenceId();
    return {
        isPlaying: state.sequencer.isPlaying[id] || false,
        resolution: state.sequencer.displayResolution,
    };
};
 
export default connect(mapStateToProps, undefined)(MiniTrackComponent);

