const NORTH = 'NORTH';
const EAST = 'EAST';
const WEST = 'WEST';
const SOUTH = 'SOUTH';

export const useBorderRadius = ({annotationLocation, location}) => {
    let style = {};
    let direction, points;
    if (location) {
        let [dir, pts] = getPoints(annotationLocation, location)
        direction = dir;
        points = pts;
        if (direction === NORTH) {
            style.borderBottomLeftRadius = '12px';
            style.borderBottomRightRadius = '12px';
        } else if (direction === EAST) {
            style.borderBottomLeftRadius = '12px';
            style.borderTopLeftRadius = '12px';
        } else if (direction === WEST) {
            style.borderBottomRightRadius = '12px';
            style.borderTopRightRadius = '12px';
        } else if (direction === SOUTH) {
            style.borderTopLeftRadius = '12px';
            style.borderTopRightRadius = '12px';
        }
    };

    return {style, direction, points};
};


// location is the "location of the element being annotated in the editor"
// annotationLocation is the "location of the annotation overlay"
const getPoints = (annotationLocation, location) => {
    if (location.x < annotationLocation.x && location.x + location.width > annotationLocation.x && location.y > annotationLocation.y) {
        return [
            SOUTH,
            [
            annotationLocation.x, annotationLocation.y + annotationLocation.height,
            location.x, location.y,
            location.x + location.width , location.y,
            annotationLocation.x + annotationLocation.width,
            annotationLocation.y + annotationLocation.height
        ]
        ];
    } else if (location.y + location.height < annotationLocation.y) {
        return [
            NORTH,
            [
            annotationLocation.x, annotationLocation.y,
            location.x, location.y + location.height,
            location.x + location.width , location.y + location.height,
            annotationLocation.x + annotationLocation.width,
            annotationLocation.y
        ]
        ];
    } else if (location.y + location.height > annotationLocation.y + annotationLocation.height &&
              location.y < annotationLocation.y) {
        return [EAST,
                [
                    annotationLocation.x + annotationLocation.width, annotationLocation.y,
                    location.x, location.y,
                    location.x, location.y + location.height,
                    annotationLocation.x + annotationLocation.width,
                    annotationLocation.y+annotationLocation.height
                ]
               ];
    } else if (location.y + location.height > annotationLocation.y &&
              location.x < annotationLocation.x) {
        return [
            WEST,
            [
            annotationLocation.x, annotationLocation.y,
            location.x + location.width , location.y,
            location.x + location.width, location.y + location.height,
            annotationLocation.x, annotationLocation.y + annotationLocation.height
        ]
        ];
    } 

    return [EAST,
            [
        annotationLocation.x + annotationLocation.width, annotationLocation.y,
        location.x, location.y,
        location.x, location.y + location.height,
        annotationLocation.x + annotationLocation.width,
        annotationLocation.y+annotationLocation.height
            ]
           ];
};

